import React from 'react';
import '../stylesheets/Filters.scss';

class Filters extends React.Component {
  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    this.state = { name: '' };
  }
  // Creamos el estado para guardar los datos que nos vienen del imput ( en este caso será el nombre de la serie)
  handleChange(event) {
    this.setState({ name: event.target.value });
  }
  render() {
    return (
      <>
        <form className="form" autoComplete="off">
          <div className="input">
            <label htmlFor="inputSearch"></label>
            <input
              className="input__search"
              type="text"
              onChange={this.handleChange}
              value={this.state.search}
              placeholder="Encuentra tu personaje favorito"
            />
            <button className="removeBtn" type="button">
              X
            </button>
          </div>
          <div className="input checkbox">
            <div>
              <i className="fas fa-sort-alpha-down icon"></i>
              <label
                className="input__checkbox--label"
                title="Ordenar por nombre"
              >
                Ordenar por nombre
              </label>
              <input
                className="input__checkbox"
                type="checkbox"
                name="orderByName"
                title="Ordenar por nombre"
              />
            </div>
            <div>
              <i className="fab fa-reddit-alien icon"></i>
              <label
                className="input__checkbox--label"
                title="Traducir a alienígena"
              >
                Traducir a <span className="alien">alienígena</span>
              </label>
              <input
                className="input__checkbox"
                type="checkbox"
                value="translateToAlien"
                title="Traducir a alienígena"
              />
            </div>
            <div>
              <i className="fas fa-skull-crossbones icon"></i>
              <label
                className="input__checkbox--label"
                htmlFor="checkboxDead"
                title="¿Quién está muerto?"
              >
                ¿Quién está muerto?
              </label>
              <input
                className="input__checkbox"
                type="checkbox"
                title="¿Quién está muerto?"
              />
            </div>
          </div>
        </form>
      </>
    );
  }
}

export default Filters;
